<template>
  <el-card class="box-card">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="30">
      <el-col :span="8">
        <!--  -->
        <el-input placeholder="" v-model="serachKey" autofocus="true" clearable>
          <el-button slot="append" icon="el-icon-search" @click="handleSearch(serachKey)"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <!--  -->
        <el-button type="primary" @click="handleAddBooks" v-if="addBook"></el-button>
      </el-col>
    </el-row>

    <!--  -->
    <el-table
      :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
      style="width: 110%"
      border
      row-key
      :header-cell-style="{color:'#606266'}"
      class="booktable"
      max-width="250"
    >
      <!-- / -->
      <el-table-column prop="name" label="" align="center"></el-table-column>
      <el-table-column prop="zuozhe" label="" align="center"></el-table-column>
      <el-table-column prop="chubanshe" label="" align="center"></el-table-column>
      <el-table-column prop="money" label="" align="center"></el-table-column>
      <el-table-column prop="shuliang" label="" align="center" v-if="btnBs"></el-table-column>
      <el-table-column prop="something" label align="center" width="200" v-if="btnSome">
        <template slot-scope="scope">
          <el-button
            type="primary"
            v-show="btnAdd"
            icon="el-icon-circle-plus-outline"
            circle
            @click="handleCar(scope.$index,scope.row)"
          ></el-button>
          <el-button
            type="primary"
            v-show="btnGai"
            icon="el-icon-edit"
            circle
            @click="handleTrue(scope.$index,scope.row)"
          ></el-button>
          <el-button
            type="danger"
            v-show="btnEdd"
            icon="el-icon-delete"
            circle
            @click="handleDelete(scope.$index,scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible="addBookVisible"
      width="30%"
      :before-close="handleAddBookFalse"
      @close="EditBookData"
    >
      <el-form
        :model="addUsers"
        :rules="addUsersRules"
        label-width="70px"
        class="demo-ruleForm"
        ref="addUsers"
      >
        <el-form-item label="" prop="addBookname">
          <el-input v-model="addUsers.addBookname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="addBooktitle">
          <el-input v-model="addUsers.addBooktitle" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="addBookprice">
          <el-input v-model="addUsers.addBookprice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="addBooknum">
          <el-input v-model="addUsers.addBooknum" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="addBooknumber">
          <el-input v-model="addUsers.addBooknumber" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addBookVisible = false"> </el-button>
        <el-button type="primary" @click="handleAddBookTrue"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible="dialogVisible" width="30%" :before-close="handleFalse">
      <el-form
        :model="editBook"
        :rules="editBookRules"
        label-width="70px"
        class="demo-ruleForm"
        ref="editBook"
      >
        <el-form-item label="" prop="bookname">
          <el-input v-model="editBook.bookname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="remanname">
          <el-input v-model="editBook.remanname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="bookprice">
          <el-input v-model="editBook.bookprice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="booknum">
          <el-input v-model="editBook.booknum" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="booknumber">
          <el-input v-model="editBook.booknumber" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleEditTrue"> </el-button>
      </span>
    </el-dialog>
    <div
      class="bookcar"
      @click="drawer = true"
      type="primary"
      style="margin-left: 16px;"
      v-show="offCar"
    >
      <i class="el-icon-s-promotion"></i>
    </div>
    <el-drawer title="" :visible.sync="drawer" :with-header="false">
      <div class="drtop">
        <div class="title">
          <el-page-header @back="drawer = false" content=""></el-page-header>
          <br />
          <el-row :gutter="12">
            <el-col :span="24">
              <el-card shadow="hover" body-style="width:100%;">
                <el-table
                  stripe
                  max-height="450"
                  :data="allBook.filter(data => !search ||
                   data.name.toLowerCase().includes(search.toLowerCase())||
                   data.zuozhe.toLowerCase().includes(search.toLowerCase())||
                   data.chubanshe.toLowerCase().includes(search.toLowerCase())||
                   data.money.toLowerCase().includes(search.toLowerCase())
                   )"
                  style="width: 130%"
                >
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="" >
                          <span>{{ props.row.name }}</span>
                        </el-form-item>
                        <el-form-item label="">
                          <span>{{ props.row.zuozhe }}</span>
                        </el-form-item>
                        <el-form-item label="">
                          <span>{{ props.row.chubanshe }}</span>
                        </el-form-item>
                        <el-form-item label="">
                          <span>{{ props.row.money }}</span>
                        </el-form-item>
                        <el-form-item label="">
                          <span>{{ props.row.shuliang }}</span>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="" prop="name" align="center"></el-table-column>
                  <el-table-column label="" prop="money" align="center"></el-table-column>
                  <el-table-column align="right" fixed >
                    <template slot="header" slot-scope="scope">
                      <el-input v-model="search" size="mini" placeholder="" />
                    </template>
                    <template slot-scope="scope">
                      <button @click="handleEdd(scope.$index,scope.row)">-</button>
                      <span
                        style="width:30px;display:-moz-inline-box;display:inline-block;text-align:center; "
                      >{{scope.row.count}}</span>
                      <button @click="handleAdd(scope.row)">+</button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
            </el-col>
          </el-row>
          <div class="aeBook">
            <div class="div1">{{bookMoney()}}</div>
            <div class="div2">
              <el-button type="primary" round @click="teaBlist"></el-button>
              <el-button type="success" round @click="resAllBooks"></el-button>
            </div>
          </div>
        </div>
      </div>
    </el-drawer>
    <!--  -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="tableRelevant.pageNow"
      background
      :page-sizes="[1, 3, 5, 10]"
      :page-size="tableRelevant.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
    ></el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      // bookMoney:0,//
      search: "",
      allBook: [], //
      drawer: false, //
      addBook: true, //
      offCar: true, //
      btnAdd: true, //
      btnEdd: true, //
      btnGai: true, //
      btnBs: true, //
      btnSome: true, //
      serachKey: "", //
      tableRelevant: {
        pageNow: 1, //
        pageSize: 5, //
        index: "" //
      },
      tableData: [], //
      addBookVisible: false, //
      dialogVisible: false, //
      addUsers: {
        //
        addBookname: "",
        addBooktitle: "",
        addBookprice: "",
        addBooknum: "",
        addBooknumber: "" //
      },
      addUsersRules: {
        //
        addBookname: [
          { required: true, message: "", trigger: "blur" }
        ],
        addBooktitle: [
          { required: true, message: "", trigger: "blur" }
        ],
        addBookprice: [
          { required: true, message: "", trigger: "blur" }
        ],
        addBooknum: [
          { required: true, message: "", trigger: "blur" }
        ],
        addBooknumber: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      editBook: {
        //
        bookname: "",
        remanname: "",
        bookprice: "",
        booknum: "",
        booknumber: "" //
      },
      editBookRules: {
        //
        bookname: [
          { required: true, message: "", trigger: "blur" }
        ],
        remanname: [
          { required: true, message: "", trigger: "blur" }
        ],
        bookprice: [
          { required: true, message: "", trigger: "blur" }
        ],
        booknum: [
          { required: true, message: "", trigger: "blur" }
        ],
        booknumber: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  watch: {
    serachKey() {
      //
      if (this.serachKey == "") {
        this.getUsersInFo();
      }
    }
  },
  created() {
    this.getUsersInFo(); //
    var sess = window.sessionStorage.getItem("nav");
    if (sess == "menugly") {
      this.addBook = false;
      this.btnSome = false;
      this.offCar = false;
      this.btnAdd = false;
      this.btnEdd = false;
      this.btnGai = false;
      this.btnBs = true;
    } else if (sess == "menujs") {
      this.addBook = false;
      this.btnSome = true;
      this.offCar = true;
      this.btnAdd = true;
      this.btnEdd = false;
      this.btnGai = false;
      this.btnBs = false;
    } else if (sess == "menusd") {
      this.offCar = false;
      this.btnBs = true;
      this.btnAdd = false;
    }
  },
  methods: {
    getUsersInFo() {    //
        this.$api.SearchB().then(res => {
            if(res.code=='1'){
                const data = res.list
                  this.tableData = data
        console.log(res)
            }else{console.log('')}
        }).catch(function(error){console.log(error)})
    },

    // getUsersInFo() {
    //   //
    //   this.$api.Mock().then(res => {
    //     res=res.data
    //     this.tableData = res.books;
    //   });
    // },
    handleSizeChange(newSize) {
      //pageSize()
      this.tableRelevant.pageSize = newSize;
    },
    handleCurrentChange(newNow) {
      //pageNow()
      this.tableRelevant.pageNow = newNow;
    },
    handleSearch(serachKey) {
      //
      const mineDatas = [];
      this.tableData.forEach((value, index) => {
        if (
          value.name.indexOf(serachKey) > -1 ||
          value.zuozhe.indexOf(serachKey) > -1 ||
          value.chubanshe.indexOf(serachKey) > -1
        ) {
          mineDatas.push(value);
        }
      });
      this.tableData = mineDatas; //
    },
    handleCar(index, res) {
      //
      if (this.allBook.indexOf(res) === -1) {
        this.$set(res, "count", "0");
        this.allBook.push(res);
        console.log(this.allBook)  
      }
    },
    bookMoney() {
      //
      var ipprice = 0;
      for (var i = 0; i < this.allBook.length; i++) {
        ipprice += this.allBook[i].money * this.allBook[i].count;
      }
      return ipprice;
    },
    handleEdd(index, res) {
      //-
      res.count--;
      if (res.count < 0) {
        res.count = 0;
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message.ms('');
            this.allBook.splice(index, 1);
          })
          .catch(() => {
            this.$message.mi('!');
          });
      }
    },
    handleAdd(res) {
      //+
      res.count++;
    },
    teaBlist() {
      //
      let params = {
        allbook: this.allBook
      };
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message.ms('');
          this.$api.teaBlist(params)
            .then(res => {
              console.log(res);
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {
          this.$message.mi('');
        });
    },
    resAllBooks() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message.ms('!')
          for (let i = this.allBook.length - 1; i >= 0; i--) {
            this.allBook.splice(i, 1);
          }
          console.log(this.allBook)
        })
        .catch(() => {
          this.$message.mi('');
        });
    },
    handleAddBooks() {
      //
      this.addBookVisible = true;
    },
    handleAddBookFalse() {
      //
      this.addBookVisible = false;
    },
    EditBookData() {
      this.$refs.addUsers.resetFields(); //
    },
    handleAddBookTrue() {
      //
      this.addBookVisible = false;
      this.$refs.addUsers.validate(valid => {
        //
        if (!valid) return;
        const addBook = {
          title: this.addUsers.addBooktitle,
          name: this.addUsers.addBookname,
          price: this.addUsers.addBookprice,
          number: this.addUsers.addBooknum
        };
        let params={ //
          book: this.addUsers.addBooktitle,
          zuozhe: this.addUsers.addBookname,
          password: this.addUsers.addBooknum,
          number: this.addUsers.addBookprice,
          shuliang: this.addUsers.addBooknumber
        }
        this.$api.AddBook(params).then(res => {
            if(res.code=='1'){
                this.getUsersInFo()
                this.$message({
                    message: '',
                    type: 'success'
                });
            }else{
                this.$message({
                    message: '',
                    type: 'warning'
                });
            }
        }).catch(function (error) { console.log(error) })

        this.$message.success("!");
        setTimeout(() => {
          location.reload();
        }, 2000);
      });
    },
    handleTrue(index, res) {
      //
      this.dialogVisible = true;
      this.tableRelevant.index = index;
      // console.log(index)
    },
    handleFalse() {
      //
      this.dialogVisible = false;
    },
    handleEditTrue() {
      //
      if (
        this.editBook.bookname == "" ||
        this.editBook.remanname == "" ||
        this.editBook.bookprice == "" ||
        this.editBook.booknum == ""
      ) {
        this.$message({
          message: "!"
        });
      } else {
        this.tableData[this.tableRelevant.index].title = this.editBook.bookname
        this.tableData[this.tableRelevant.index].name = this.editBook.remanname
        this.tableData[this.tableRelevant.index].price = this.editBook.bookprice
        this.tableData[this.tableRelevant.index].number = this.editBook.booknum
        let params={   //
          book: this.editBook.bookname,
          zuozhe: this.editBook.remanname,
          password: this.editBook.booknum,
          number: this.editBook.bookprice,
          shuliang: this.editBook.booknumber
        }
        this.$api.ModifyBook(params).then(res => {
                if(res.code=='1'){
                    this.getUsersInFo()
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                }else{
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            }).catch(function (error) { console.log(error) })

        // this.$message.success("!");
      }
      this.dialogVisible = false;
      this.$refs.editBook.resetFields(); //
    },
    async handleDelete(index, res) {
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      // this.$message.success("");
      let params={
        num:index
      }
      this.$api.DeleteBook(params).then(res => {
                  if(res.code=='1'){
                      this.getUsersInFo()
                      this.$message({
                          message: '',
                          type: 'success'
                      });
                  }else{
                      this.$message({
                          message: '',
                          type: 'warning'
                      });
                  }
              }).catch(function (error) { console.log(error) })
      this.tableData.splice(index, 1)
    },
    handleClose() {
      this.drawer = false;
    }
  }
};
</script>

<style scoped lang='stylus'>


.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 90%;
}

.bookcar {
  font-size: 200%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.title {
  .el-card {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;

    .el-input-number {
      flex: 1;
    }
  }

  .aeBook {
    width: 100%;
    height: 50px;
    background-color: #EBEEF5;
    position: fixed;
    bottom: 0;
    display: flex;

    .div1 {
      width: 120px;
      height: 50px;
      line-height: 50px;
      text-align: center;
    }

    .div2 {
      line-height: 50px;
    }
  }
}
</style>